	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Anyone ever give you advice on how to remain a programmer? To avoid being \"promoted\" into positions away from technology and code? Anyone ever tell you at school or university that you needed social skills to be a good developer? Did you know, without having had half a dozen different jobs, that all development roles are not created equal? Is it true that moving jobs a lot is a Bad Thing?\n\nIn this session I'm going to share some lessons I learnt the hard way while managing my career as a Java developer. I'm going to tell you secrets that others don't want to share. And I'll give you tools for working out what your next steps are.\n\nIf nothing else, you'll get to laugh at the (many) mistakes I've made in my search for The Perfect Job.","meta":{"duration":"PT54M19S"},"presenter":{"name":"Trisha Gee"},"source":{"id":"LlAn452X4Lc","provider":"youtube"},"slug":"career-advice-for-programmers","tag":["Career","The perfect Job","GOTO Conference"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/LlAn452X4Lc/default.jpg","height":90,"width":120}},"title":"Career Advice for Programmers"}}]}},"pathContext":{"slug":"the-perfect-job","tag":"The perfect Job"}}

